<template>
  <div>
    <h1>Shopping Cart</h1>
    <div v-if="cartItems.length > 0">
      <shopping-cart-list
        :cartItems="cartItems"
        @remove-from-cart="removeFromCart($event)"
      />
      <button v-if="cartItems.length != 0" class="checkout-button">
        Checkout
      </button>
    </div>
    <div v-if="cartItems.length === 0">
      You current have no items in your cart!
    </div>
  </div>
</template>

<script>
import ShoppingCartList from "@/components/ShoppingCartList.vue";
import axios from "axios";

export default {
  name: "ShoppingCartPage",
  data() {
    return {
      cartItems: [],
      props: ["user"],
    };
  },
  components: {
    ShoppingCartList,
  },
  watch: {
    async user(newUserValue) {
      if (newUserValue) {
        const cartResponse = await axios.get(
          `/api/users/${newUserValue.uid}/cart`
        );
        const cartItems = cartResponse.data;
        this.cartItems = cartItems;
      }
    },
  },
  methods: {
    async removeFromCart(id) {
      try {
        const response = await axios.delete(`/api/users/12345/cart/${id}`);
        const cartItems = response.data;
        this.cartItems = cartItems.filter((item) => item !== null);
      } catch (error) {
        console.error("There was an error!", error);
      }
    },
  },
  async created() {
    const response = await axios.get(`/api/users/${this.user.uid}/cart`);
    const cartItems = response.data;
    if (cartItems !== null) {
      if (this.user) {
        this.cartItems = cartItems.filter((item) => item !== null);
      }
    }
  },
};
</script>
