<template>
  <div v-if="product">
    <div class="img-wrap">
      <img :src="product.imageName" />
    </div>
    <div class="product-details">
      <h1>{{ product.name }}</h1>
      <h3 class="price">{{ product.price }}</h3>
      <button
        @click="addToCart"
        class="add-to-cart"
        v-if="user && !itemIsInCart"
      >
        Add to Cart
      </button>
      <button class="grey-button" v-if="user && itemIsInCart">
        Item already in card
      </button>
      <button class="sign-in" @click="signIn" v-if="!user">
        Sign in to add to cart
      </button>
    </div>
  </div>
  <div v-else>
    <NotFoundPage />
  </div>
</template>

<script>
import {
  getAuth,
  sendSignInLinkToEmail,
  isSignInWithEmailLink,
  signInWithEmailLink,
} from "firebase/auth";
import NotFoundPage from "./NotFoundPage.vue";
import axios from "axios";

const apiBaseUrl = "https://full-stack-vue-deployment-7ss0.onrender.com";
export default {
  name: "productDetailPage",
  prop: ["user"],
  data() {
    return {
      product: {},
      cartItems: [],
    };
  },
  computed: {
    itemIsInCart() {
      return this.cartItems.some(
        (item) => item !== null && item.id === this.$route.params.id
      );
    },
  },
  watch: {
    async user(newUserValue) {
      if (newUserValue) {
        const cartResponse = await axios.get(
          `${apiBaseUrl}/api/users/${newUserValue.uid}/cart`
        );
        this.cartItems = cartResponse.data;
      }
    },
  },
  methods: {
    async addToCart() {
      try {
        await axios.post(`/api/users/${this.user.uid}/cart`, {
          id: this.$route.params.id,
        });
        alert("Added to cart!");
      } catch (error) {
        console.error("There was an error!", error);
      }
    },
    async signIn() {
      const email = prompt("Please enter your email");
      const auth = getAuth();
      const actionCodeSettings = {
        url: `https://full-stack-vue-deployment-7ss0.onrender.com/products/${this.$route.params.id}`,
        handleCodeInApp: true,
      };
      await sendSignInLinkToEmail(auth, email, actionCodeSettings);
      alert(`An email has been sent to ${email}. Click the link to sign in.`);
      window.localStorage.setItem("emailForSignIn", email);
    },
  },
  components: {
    NotFoundPage,
  },
  async created() {
    const auth = getAuth();
    if (isSignInWithEmailLink(auth, window.location.href)) {
      let email = window.localStorage.getItem("emailForSignIn");
      if (!email) {
        email = window.prompt("Please provide your email for confirmation");
      }
      await signInWithEmailLink(auth, email, window.location.href);
      alert("You have been signed in!");
      window.localStorage.removeItem("emailForSignIn");
    }
    try {
      const response = await axios.get(
        `/api/products/${this.$route.params.id}`
      );
      this.product = response.data;
      if (this.user) {
        const cartResponse = await axios.get(
          `/api/users/${this.user.uid}/cart`
        );
        this.cartItems = cartResponse.data;
      }
    } catch (error) {
      console.error("There was an error!", error);
    }
  },
};
</script>
