import { createApp } from "vue";
import App from "./App.vue";
import "./main.css";
import * as VueRouter from "vue-router";
import ShoppingCartPage from "./pages/ShoppingCartPage.vue";
import ProductsPage from "./pages/ProductsPage.vue";
import ProductDetailPage from "./pages/ProductDetailPage.vue";
import NotFoundPage from "./pages/NotFoundPage.vue";

import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: "AIzaSyC2kYfmvlY3s1W46tDmZ6F66m6GPo9GnGU",
  authDomain: "vue-site-8bd32.firebaseapp.com",
  projectId: "vue-site-8bd32",
  storageBucket: "vue-site-8bd32.appspot.com",
  messagingSenderId: "207298039126",
  appId: "1:207298039126:web:01cbd493fbfc8a1fbedaf7",
};

initializeApp(firebaseConfig);

createApp(App)
  .use(
    VueRouter.createRouter({
      history: VueRouter.createWebHistory(),
      routes: [
        { path: "/products", component: ProductsPage },
        { path: "/cart", component: ShoppingCartPage },
        { path: "/products/:id", component: ProductDetailPage },
        { path: "/", redirect: "/products" },
        { path: "/:pathMatch(.*)*", component: NotFoundPage },
      ],
    })
  )
  .mount("#app");
